<template>
    <div class="card rounded-0">
        <div class="landing__header dark-background card-header rounded-0 p-4">
            <h3>
                {{ $route.params.query  }}
                <span>
                    <BetButton @click="$router.go(-1)" class="float-right">
                    <i class="fa fa-arrow-left"></i> Back
                </BetButton>
                </span>
            </h3>

        </div>

        <div class="landing__main card-body p-0">
            <div class="text-center">
                <LoadingIndicator v-if="isLoading"></LoadingIndicator>
                <NoGames v-if="!isLoading && games && !games.length"></NoGames>
            </div>
            <GamesComponent :games="games"></GamesComponent>
        </div>
    </div>
</template>

<script>
  import LoadingIndicator from "../Utilities/LoadingIndicator";
  import axios from 'axios'
  import GamesComponent from "./GamesComponent";
  import {SEARCH_GAME_URL} from "../../api_routes";
  import NoGames from "../Utilities/NoGames";
  import BetButton from "../Utilities/BetButton";

  export default {
    name: 'SearchResults',
    components: {BetButton, NoGames, GamesComponent, LoadingIndicator},
    data () { return { isLoading: false, games: null }},
    mounted () {
        this.searchGames()
    },
      methods: {
      searchGames () {
          this.isLoading = true;
          axios.post(SEARCH_GAME_URL, { filters: this.$route.params.query}).then((resp) => {
              if (resp.status === 200) {
                  let responseData = resp.data.data
                  this.games = responseData
                  this.isLoading = false
              }
          }, () => this.isLoading = false )
      }
    }
  }
</script>

